.loginForm-seperator {
  margin: 15px 0;
  text-align: center;
}
.loginForm-seperator span.text {
  font-weight: bold;
}
.btn.provider-google {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #dd4b39;
  color: #fff;
  background: #dd4b39;
}
.btn.provider-google:hover {
  border: 1px solid #c23321;
  background: #c23321;
  color: #fff;
}
.btn.provider-google i {
  margin-right: 10px;
  font-size: 1.6rem;
}
