.checkbox-switch {
  width: 36px;
  height: 22px;
  position: relative;
  cursor: hand;
  padding: 10px;
}
.checkbox-switch input,
.checkbox-switch label {
  display: none;
}
.checkbox-switch:before {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  display: block;
  background-color: #e6e6e6;
  width: 100%;
  height: 22px;
  border-radius: 12px;
  /* box-shadow: 0 0 0 2px darken(#fff, 10%); */
  /* border: 2px solid darken(#fff, 10%); */
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}
.checkbox-switch:after {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-color: #fff;
  position: absolute;
  left: 2px;
  top: 2px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}
.checkbox-switch:avtive:after {
  background-color: #e6e6e6;
}
.checkbox-switch:checked:before {
  background-color: #1999dd;
  /* box-shadow: 0 0 0 2px green; */
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}
.checkbox-switch:checked:after {
  left: 16px;
}
.checkbox-switch:disabled {
  opacity: 0.6;
}
.rui.switch {
  display: flex;
  align-items: center;
  cursor: hand;
  padding: 10px 0;
  margin-bottom: 0;
}
.rui.switch input,
.rui.switch label {
  display: none;
}
.rui.switch .switch-control {
  position: relative;
  width: 36px;
  max-width: 36px;
  height: 22px;
  flex: 0 0 auto;
}
.rui.switch .switch-control:before {
  position: absolute;
  left: 0;
  top: 6px;
  content: "";
  display: block;
  background-color: #e6e6e6;
  width: 100%;
  height: 10px;
  border-radius: 12px;
  /* box-shadow: 0 0 0 2px darken(#fff, 10%); */
  /* border: 2px solid darken(#fff, 10%); */
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}
.rui.switch .switch-control:after {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-color: #777777;
  position: absolute;
  left: 0;
  top: 2px;
  border-radius: 10px;
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}
.rui.switch .switch-control.active:before {
  background-color: #6dc2ef;
  -webkit-transition: all 200ms;
  -moz-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}
.rui.switch .switch-control.active:after {
  left: 18px;
  background-color: #1999dd;
}
.rui.switch .switch-control:disabled {
  opacity: 0.6;
}
.rui.switch .control-label {
  margin-left: 10px;
}
.rui.list-item-action .rui.switch {
  padding: 0;
}
